
.md-theme-default .markdown-body pre code {
  background-color: inherit;
  color: inherit;
}
.markdown-body a {
  color: #4183c4;
}
.markdown-body a.absent {
  color: #c00;
}
.markdown-body a.anchor {
  display: block;
  padding-left: 30px;
  margin-left: -30px;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
}
.markdown-body a:first-child h1,
.markdown-body a:first-child h2,
.markdown-body a:first-child h3,
.markdown-body a:first-child h4,
.markdown-body a:first-child h5,
.markdown-body a:first-child h6 {
  margin-top: 0;
  padding-top: 0;
}
.markdown-body h1,
.markdown-body h2,
.markdown-body h3,
.markdown-body h4,
.markdown-body h5,
.markdown-body h6 {
  margin: 20px 0 10px;
  padding: 5px 0;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  cursor: text;
  position: relative;
  line-height: 1;
}
.markdown-body h1:hover a.anchor,
.markdown-body h2:hover a.anchor,
.markdown-body h3:hover a.anchor,
.markdown-body h4:hover a.anchor,
.markdown-body h5:hover a.anchor,
.markdown-body h6:hover a.anchor {
  text-decoration: none;
}
.markdown-body h1 tt,
.markdown-body h2 tt,
.markdown-body h3 tt,
.markdown-body h4 tt,
.markdown-body h5 tt,
.markdown-body h6 tt,
.markdown-body h1 code,
.markdown-body h2 code,
.markdown-body h3 code,
.markdown-body h4 code,
.markdown-body h5 code,
.markdown-body h6 code {
  font-size: inherit;
}
.markdown-body h1 p,
.markdown-body h2 p,
.markdown-body h3 p,
.markdown-body h4 p,
.markdown-body h5 p,
.markdown-body h6 p {
  margin-top: 0;
}
.markdown-body h1 {
  font-size: 28px;
  color: #000;
}
.markdown-body h2 {
  font-size: 24px;
  border-bottom: 1px solid #ccc;
  color: #000;
}
.markdown-body h3 {
  font-size: 18px;
}
.markdown-body h4 {
  font-size: 16px;
}
.markdown-body h5 {
  font-size: 14px;
}
.markdown-body h6 {
  color: #777;
  font-size: 14px;
}
.markdown-body hr {
  border: 0 none;
  color: #ccc;
  height: 4px;
  padding: 0;
}
.markdown-body li {
  margin: 0;
}
.markdown-body li p.first {
  display: inline-block;
}
.markdown-body dl {
  padding: 0;
}
.markdown-body dl dt {
  font-size: 14px;
  font-weight: bold;
  font-style: italic;
  padding: 0;
  margin: 15px 0 5px;
}
.markdown-body dl dt:first-child {
  padding: 0;
}
.markdown-body dl dt > :first-child {
  margin-top: 0;
}
.markdown-body dl dt > :last-child {
  margin-bottom: 0;
}
.markdown-body dl dd {
  margin: 0 0 15px;
  padding: 0 15px;
}
.markdown-body dl dd > :first-child {
  margin-top: 0;
}
.markdown-body dl dd > :last-child {
  margin-bottom: 0;
}
.markdown-body blockquote {
  border-left: 4px solid #ddd;
  padding: 0 15px;
  color: #777;
}
.markdown-body blockquote > :first-child {
  margin-top: 0;
}
.markdown-body blockquote > :last-child {
  margin-bottom: 0;
}
.markdown-body .doc-table table {
  min-width: 600px;
  padding: 0;
  border-collapse: collapse;
}
.markdown-body .doc-table tr {
  background-color: #fff;
  margin: 0;
  padding: 0;
}
.markdown-body .doc-table tr:nth-child(2n + 1) td {
  background-color: #f8f8f8;
}
.markdown-body .doc-table tr th {
  font-weight: bold;
  margin: 0;
  padding: 10px 13px;
  border-bottom: 1px solid #eee;
  text-align: left;
}
.markdown-body .doc-table tr td {
  margin: 0;
  padding: 10px 13px;
}
.markdown-body .doc-table tr th,
.markdown-body .doc-table tr td {
  min-width: 100px;
}
.markdown-body .doc-table tr th:first-child,
.markdown-body .doc-table tr td:first-child {
  margin-top: 0;
}
.markdown-body .doc-table tr th:last-child,
.markdown-body .doc-table tr td:last-child {
  margin-bottom: 0;
}
.markdown-body img {
  max-width: 100%;
}
.markdown-body span.frame {
  display: block;
  overflow: hidden;
}
.markdown-body span.frame > span {
  border: 1px solid #ddd;
  display: block;
  float: left;
  overflow: hidden;
  margin: 13px 0 0;
  padding: 7px;
  width: auto;
}
.markdown-body span.frame span span {
  clear: both;
  color: #333;
  display: block;
  padding: 5px 0 0;
}
.markdown-body span.frame span img {
  display: block;
  float: left;
}
.markdown-body span .align-center {
  display: block;
  overflow: hidden;
  clear: both;
}
.markdown-body span .align-center > span {
  display: block;
  overflow: hidden;
  margin: 13px auto 0;
  text-align: center;
}
.markdown-body span .align-center span img {
  margin: 0 auto;
  text-align: center;
}
.markdown-body span .align-right {
  display: block;
  overflow: hidden;
  clear: both;
}
.markdown-body span .align-right > span {
  display: block;
  overflow: hidden;
  margin: 13px 0 0;
  text-align: right;
}
.markdown-body span .align-right span img {
  margin: 0;
  text-align: right;
}
.markdown-body span .float-left {
  display: block;
  margin-right: 13px;
  overflow: hidden;
  float: left;
}
.markdown-body span .float-left span {
  margin: 13px 0 0;
}
.markdown-body span .float-right {
  display: block;
  margin-left: 13px;
  overflow: hidden;
  float: right;
}
.markdown-body span .float-right > span {
  display: block;
  overflow: hidden;
  margin: 13px auto 0;
  text-align: right;
}
.markdown-body code,
.markdown-body tt {
  margin: 0 2px;
  padding: 0 5px;
  white-space: nowrap;
  border: 1px solid #eaeaea;
  background-color: #f8f8f8;
  border-radius: 3px;
}
.markdown-body pre code {
  margin: 0;
  padding: 0;
  white-space: pre;
  border: none;
  background: transparent;
}
.markdown-body pre {
  border: 1px solid #ccc;
  font-size: 13px;
  line-height: 19px;
  overflow: auto;
  padding: 6px 10px;
  border-radius: 3px;
}
.markdown-body pre code,
.markdown-body pre tt {
  background-color: transparent;
  border: none;
}
.markdown-body sup {
  font-size: 0.83em;
  vertical-align: super;
  line-height: 0;
}
.markdown-body ul:not(.md-list) > li + li {
  margin-top: 0px;
}